#flip-container{
  -webkit-perspective: 1000;
  perspective: 1000;
  padding: 500px;
  position: relative;
  margin: 10px auto;
  width: 500px;
}
.card {
  position: relative;
  width: 300px;
  height: 300px;
  transition: 1s;
  transform-style: preserve-3d;
  transform-origin: 100% 50%;
  margin: 5px;
}
.card:hover, .card.flip{
  transform: rotateX(180deg);
}
.front, .back{
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  text-align: center;
  
}
.front{
  transform: rotateX(0deg);
  z-index: 2;
  background-color:  rgba(42, 42, 42, 0.5);
  color: #FFF;
  font: 1em/1.8em Arial;
  display: flex;
  align-items: center;
  justify-content: center;

}
.back{
  transform: rotateX(180deg);
  background-color:  rgba(42, 42, 42, 0.5);
  color: #656363;

}
